nav {
  background: rgba(163, 163, 163, 0.096);
  width: max-content;
  display: block;
  padding: 0.7rem 1.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 3rem;
  bottom: 2rem;
  gap: 0.8rem;
  display: flex;
  backdrop-filter: blur(15px);
}

nav a {
  background: transparent;
  padding: 0.6rem;
  display: flex;
  border-radius: 50%;
  color: var(--color-light);
  font-size: 1.1rem;
}
nav a:hover {
  background: rgba(0, 0, 0, 0.452);
}

nav a.active {
  background: rgba(0, 0, 0, 0.452);
  color: var(--color-white);
}
