header {
  height: 100vh;
  padding-top: 4rem;
  overflow: hidden;
}
.header_container {
  text-align: center;
  height: 100%;
  position: relative;
}

/* =================CTA btn=========== */
.cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1.2rem;
  justify-content: center;
}

/* ================= Social Icons=========== */

.header_social {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_social::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--color-primary);
}

/* ================= ME =========== */

.me {
  background: linear-gradient(var(--color-primary), transparent, transparent);
  width: 11.5rem;
  height: 40rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  margin-top: 3rem;
  border-radius: 12rem 12rem 0 0;
  padding-left: 1rem;
}

/* ================= Scroll Down=========== */

.scroll_down {
  position: absolute;
  right: -2.4rem;
  bottom: 5rem;
  transform: rotate(90deg);
  font-weight: 300;
}

/* ====================== Media Query (medium devices)======================= */

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
}

/* ====================== Media Query (Small devices)======================= */

@media screen and (max-width: 600px) {
  header {
    height: 100vh;
    padding-top: 7rem;
  }
  .header_social,
  .scroll_down {
    display: none;
  }
  .me {
    height: 60rem;
    width: 20rem;
    margin-top: 6.5rem;
  }
}
