.portfolio_container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2.5rem;
}

.portfolio_item {
  background-color: var(--color-bg-variant);
  padding: 1.3rem;
  border: 1px solid transparent;
  border-radius: 2rem;
}

.portfolio_item-image img {
  border-radius: 1rem;
}

.portfolio_item h3 {
  margin: 1rem;
  font-size: 15px;
}

.portfolio_item .cta {
  margin-top: 2.5rem;
  display: flex;
  gap: 1rem;
  justify-content: center;
}

.portfolio_item:hover {
  background-color: transparent;
  border-color: var(--color-bg-variant);
  backdrop-filter: blur(5px);
}

/* ====================== Media Query (medium devices)======================= */

@media screen and (max-width: 1024px) {
  .portfolio_container {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }

  .portfolio_item {
    width: 90%;
    margin: 1rem auto;
  }
  .portfolio_item .cta {
    margin-top: 1rem;
    gap: 2.5rem;
  }
}

/* ====================== Media Query (medium devices)======================= */

@media screen and (max-width: 600px) {
  .portfolio_container {
    grid-template-columns: 1fr;
    gap: 0.8rem;
  }

  .portfolio_item {
    width: 80%;
    margin: 0.5rem auto;
  }
  .portfolio_item .cta {
    margin-top: 1rem;
    gap: 2.5rem;
  }
}
